body {
    margin: 0;
    padding: 0;
    background-color:#EFEFEF;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

html,
body,
#root, 
.container {
    height:100%;
    display:flex;
    justify-content: center;
}

.loader {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:center;
}

.movies {
    display:flex;
    justify-content: space-between;
    align-items:flex-start;
    flex-wrap:wrap;
    width:90%;
    padding-top:60px;
}