.movie {
    width: 45%;
    min-width: 500px;
    padding: 15px;
    margin-right: 7px;
    margin-bottom: 50px;
    background-color: white;
    border-radius:5px;
    box-shadow:6px 6px 5px 1px rgba(0, 0, 0, 0.3);;
    display: flex;
    align-items: flex-start;
}

.movie img {
    position:relative;
    top: -40px;
    max-height: 200px;
    margin-right: 30px;
    box-shadow: 3px 3px 4px 0px rgba(0, 0, 0, .3);
}

.movie .movie__title,
.movie .movie__year {
    font-weight: 360;
}

.movie .movie__year,
.movie .movie__genres,
.movie .movie__summary {
    opacity: 0.6;
    font-size:14px;
}

.movie .movie__title {
    margin-bottom: 10px;
    font-size:21px;
}

.movie .movie__year {
    margin: 0;
}

.movie .movie__genres {
    list-style:none;
    display:flex;
    padding:0;
}

.movie__genres li {
    margin-right: 5px;
}